@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

html {
  font-size: 10px; }
  @media (max-width: 767px) {
    html {
      font-size: 8px; } }

body {
  font-size: 1.6rem;
  font-family: 'Nunito', sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

h1, h2, h3 {
  font-family: 'Nunito', sans-serif;
  font-weight: 700; }

h1 {
  position: relative;
  margin-bottom: 6rem;
  font-size: 4rem;
  display: inline-block;
  text-transform: uppercase; }
  h1:after {
    position: absolute;
    left: 0;
    bottom: -1rem;
    content: '';
    background-color: #E43D40;
    width: 100%;
    height: 4px; }
  h1 .emoji {
    font-size: 3.5rem; }

p, span {
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  letter-spacing: 1px;
  font-size: 1.8rem;
  margin-bottom: 1rem; }

.container {
  max-width: 85%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem; }

.grid-wrapper {
  display: grid; }
  .grid-wrapper.col-1 {
    grid-template-columns: 1fr; }
  .grid-wrapper.col-2 {
    grid-template-columns: repeat(1, 1fr); }
    @media (min-width: 544px) {
      .grid-wrapper.col-2 {
        grid-template-columns: repeat(1, 1fr); } }
    @media (min-width: 768px) {
      .grid-wrapper.col-2 {
        grid-template-columns: repeat(2, 1fr); } }
  .grid-wrapper.col-3 {
    grid-template-columns: repeat(1, 1fr); }
    @media (min-width: 544px) {
      .grid-wrapper.col-3 {
        grid-template-columns: repeat(2, 1fr); } }
    @media (min-width: 768px) {
      .grid-wrapper.col-3 {
        grid-template-columns: repeat(3, 1fr); } }
  .grid-wrapper.reverse {
    direction: rtl; }
  .grid-wrapper.align-center {
    align-items: center; }
  .grid-wrapper.grid-gap-1 {
    grid-gap: 1rem; }
  .grid-wrapper.grid-gap-2 {
    grid-gap: 2rem; }
  .grid-wrapper.grid-gap-3 {
    grid-gap: 3rem; }
  .grid-wrapper.grid-gap-4 {
    grid-gap: 4rem; }

