.img-fluid {
  max-width: 100%; }
  .img-fluid.rounded {
    border-radius: 2.5rem; }

.bg-grey {
  background-color: #f9f9f9; }

@-webkit-keyframes showSticky {
  from {
    -webkit-transform: translate3d(0, -30px, 0);
            transform: translate3d(0, -30px, 0); }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

@keyframes showSticky {
  from {
    -webkit-transform: translate3d(0, -30px, 0);
            transform: translate3d(0, -30px, 0); }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0); } }

.site-header {
  position: absolute;
  top: 0px;
  width: 100%;
  background-color: whitesmoke;
  border-bottom: 1px solid #bebebe;
  display: flex;
  align-items: center;
  height: 10rem;
  padding: 4rem 2rem;
  justify-content: flex-end;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  z-index: 9999; }
  .site-header.sticky {
    position: fixed;
    -webkit-animation: showSticky 600ms ease backwards;
            animation: showSticky 600ms ease backwards; }
  .site-header .navigation__list {
    list-style-type: none;
    display: inline-flex; }
  .site-header .navigation__list-item {
    padding-left: 2rem;
    padding-right: 2rem; }
  .site-header .navigation__link {
    text-decoration: none;
    text-transform: uppercase;
    -webkit-transition: all 3s ease-in-out;
    transition: all 3s ease-in-out;
    color: #333;
    font-family: 'Nunito', sans-serif;
    font-weight: 700;
    font-size: 1.8rem; }
    .site-header .navigation__link:after {
      content: "";
      display: block;
      width: 0;
      height: 3px;
      background: #F85C70;
      -webkit-transition: width .3s;
      transition: width .3s; }
    .site-header .navigation__link:hover:after {
      width: 100%; }

.about {
  margin-top: 15rem; }

.social-links {
  list-style-type: none;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem; }
  .social-links > li {
    display: inline;
    margin-right: 2rem; }
    .social-links > li .icon {
      font-size: 4rem;
      color: #E43D40;
      -webkit-transition: all .3s ease-in-out;
      transition: all .3s ease-in-out; }
      .social-links > li .icon:hover {
        color: #000; }

.card {
  position: relative;
  box-shadow: 5px 40px 80px 0px rgba(0, 0, 0, 0.05);
  border-radius: 2.5rem;
  background: #fff;
  padding: 2rem 3rem;
  border: 0; }
  .card__icon {
    display: block;
    font-size: 3rem;
    margin-bottom: 1rem; }
    .front-end .card__icon {
      color: #dc0a68; }
    .back-end .card__icon {
      color: #6a33fa; }
    .cms .card__icon {
      color: #E43D40; }
    .frameworks-tools .card__icon {
      color: #587291; }
    .best-practice .card__icon {
      color: #0CF574; }
    .business-acumen .card__icon {
      color: #FFD046; }
  .card__title {
    position: relative;
    display: inline-block;
    font-size: 2rem;
    line-height: 1.2;
    margin-bottom: 2rem; }
    .card__title:after {
      position: absolute;
      content: '';
      left: 0;
      bottom: -1rem;
      width: 100%;
      height: 4px; }
      .front-end .card__title:after {
        background-color: #dc0a68; }
      .back-end .card__title:after {
        background-color: #6a33fa; }
      .cms .card__title:after {
        background-color: #E43D40; }
      .frameworks-tools .card__title:after {
        background-color: #587291; }
      .best-practice .card__title:after {
        background-color: #0CF574; }
      .business-acumen .card__title:after {
        background-color: #FFD046; }
  .card__skills {
    list-style-type: none; }
    .card__skills > li {
      margin-bottom: .5rem; }

.portfolio {
  --color: #E43D40; }
  .portfolio.australian-institute-architects {
    --color: #0db14b; }
  .portfolio.firststep {
    --color: #6a33fa; }
  .portfolio.ie-digital-apt-tm {
    --color: #a19261; }
  .portfolio.ie-digital-movember {
    --color: #000; }
  .portfolio .media__image {
    height: 600px;
    object-fit: cover; }
  .portfolio .content {
    text-align: left; }
    .portfolio .content__title {
      display: inline-block;
      position: relative;
      text-transform: uppercase;
      padding-left: 2rem;
      margin-bottom: 2rem; }
      .portfolio .content__title:after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        background-color: var(--color);
        width: 5px;
        height: 100%; }
    .portfolio .content__company {
      color: var(--color); }
    .portfolio .content__info {
      margin: 2rem 0;
      font-size: 1.6rem; }
    .portfolio .content__cta {
      display: inline-block;
      border: 2px solid var(--color);
