      color: var(--color);
      margin-bottom: 4rem;
      text-decoration: none;
      text-transform: uppercase;
      font-weight: 700;
      padding: 1rem 3rem;
      border-radius: 25px;
      -webkit-transition: all .3s ease-in-out;
      transition: all .3s ease-in-out; }
      .portfolio .content__cta:hover {
