    .portfolio .content__tags {
      list-style-type: none; }
      .portfolio .content__tags > li {
        display: inline-block;
        background-color: var(--color);
        font-size: 1.4rem;
        color: #fff;
        padding: .5rem 1rem;
        margin-right: 1rem;
        margin-bottom: 1rem; }

